import React from 'react';
import ReactDOM from 'react-dom';
import React3 from '../../../../lib/React3';
import assert from 'assert';

const testDiv = document.createElement('div');

before(() => {
  document.body.appendChild(testDiv);

  ReactDOM.render(<React3
    key="warmup"
    width={1}
    height={1}
  />, testDiv);
});

class MockConsole {
  constructor() {
    this.prevConsole = window.console;

    this._messages = [];
    this._expectedMessages = [];
  }

  apply() {
    window.console = this;
  }

  revert() {
    window.console = this.prevConsole;

    const {
      _messages,
      _expectedMessages,
      } = this;

    if (_messages.length > 0) {
      assert(false, `Messages received but not expected:
${_messages.map((args, i) => `${i}: ${args.join('\t')}`).join('\n')}`);
    } else if (_expectedMessages.length > 0) {
      assert(false, `Messages expected but not received:
${_expectedMessages.map((args, i) => `${i}: ${args.join('\t')}`).join('\n')}`);
    }
  }

  log(...args) {
    this.prevConsole.log.apply(this.prevConsole, ['LOG:', ...args]);

    this._messageReceived(args);
  }

  warn(...args) {
    this.prevConsole.warn.apply(this.prevConsole, ['WARN:', ...args]);

    this._messageReceived(args);
  }

  error(...args) {
    this.prevConsole.error.apply(this.prevConsole, ['ERROR:', ...args]);

    this._messageReceived(args);
  }

  _messageReceived(args) {
    const {
      _messages,
      _expectedMessages,
      } = this;

    if (_expectedMessages.length > 0) {
      this._checkMessage(_expectedMessages.shift(), args);
    } else {
      _messages.push(args);
    }
  }

  _checkMessage(expectedArgs, actualArgs) {
    const expectedMessage = `${expectedArgs.join('\t')}`;
    const actualMessage = `${actualArgs.join('\t')}`;

    if (actualMessage !== expectedMessage) {
      throw new Error(`
    Log
    error, expected
  : ${expectedMessage}
  ,
    got: ${actualMessage}`);
    }
  }

  expect(...args) {
    const {
      _messages,
      _expectedMessages,
      } = this;


    if (_messages.length > 0) {
      this._checkMessage(args, _messages.shift());
    } else {
      _expectedMessages.push(args);
    }
  }
}

let mockConsole;

beforeEach(() => {
  mockConsole = new MockConsole();

  mockConsole.apply();
});

afterEach(() => {
  ReactDOM.unmountComponentAtNode(testDiv);

  mockConsole.revert();
});

after(() => {
  document.body.removeChild(testDiv);
});

it('Mounts with error messages', () => {
  ReactDOM.render(<React3/>, testDiv);

  mockConsole.expect('Warning: Failed propType: Required prop `width` was not specified in `React3`.');
  mockConsole.expect('Warning: Failed propType: Required prop `height` was not specified in `React3`.');

  mockConsole.expect('Warning: Failed propType: Required prop `width` was not specified in `react3`.');
  mockConsole.expect('Warning: Failed propType: Required prop `height` was not specified in `react3`.');
});

it('Mounts without warnings', () => {
  ReactDOM.render(<React3
    width={800}
    height={600}
  />, testDiv);
});



